<template>
  <div class="login">
    <div v-for="(member, key) in Members" :key="key">
      <hr>
      <p>{{ member.username }}</p>
      <hr>
    </div>
    <p>
        Email
        <input type="text" v-model="email">
    </p>
    <p>
        Password
        <input type="text" v-model="password">
    </p>
    <button @click="login">login</button><hr>
  </div>
</template>

<script>
export default {
  name: "LoginView",
  props: {
    msg: String,
  },
  data() {
    return {
      User: [],
      tokens: {
        "refresh":'',
        "access":'',
      },
      Email: '',
      Password: '',
    };
  },
  methods: {
    getInfo: function(){
      this.axios
      .get("http://localhost:8000/api/user/",{headers: {
        // postmanでのAPIcal同様にJWTが必要
        // この通信がうまくいかない時はchromeの検証モード/networkから確認できる
        "Authorization": 'JWT ' + this.tokens.access,
      }
      })
      // レスポンスをMembersプロパティに格納
      .then(response => (this.User = response.data));
    },

    login: function(){
      // token取得のためのusernameとpasswordセット
      const data = {Email: this.email, Password: this.password}
      // access_token&refresh_tokenを取得
      this.axios
        .catch(error => console.log(error,data))
        .post("http://localhost:8000/api-auth/jwt/",data)
        // レスポンスを一旦tokensプロパティに格納
        .then(response => (this.tokens = response.data));
        console.log(this.tokens);
    }
  },
};
</script>

<style scoped>
.title-color {
  color: green;
}
</style>